<template>
  <div ref="container">
    <DatePicker
      v-model="date"
      v-bind="options"
      type="daterange"
      :append-to-body="false"
      class="date-picker-panel-clone"
      popper-class="custom-datepicker"
      ref="datePickerRef"
    />
  </div>
</template>

<script>
import { ref, onMounted, nextTick, watch, computed } from 'vue'

export default {
  name: 'DatePickerPanelDirect',
  props: {
    modelValue: {
      type: Array,
      default: () => []
    },
    options: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue'],
  setup(props, ctx) {
    const date = computed({
      get: () => props.modelValue,
      set: (val) => ctx.emit('update:modelValue', val)
    })
    const datePickerRef = ref()
    const container = ref()

    onMounted(async () => {
      await nextTick()
      setTimeout(() => {
        const input = container.value?.querySelector('.date-picker-panel-clone')
        if (!input) return

        const describedBy = input.getAttribute('aria-describedby')
        const popper = document.getElementById(describedBy)

        if (popper) {
          popper.style.display = 'inline-block'
          popper.style.position = 'static'
          input.style.display = 'none'
        }
      }, 100)
    })
    watch(date, () => {
      nextTick(() => {
        const input = container.value?.querySelector('.date-picker-panel-clone')
        if (!input) return

        const describedBy = input.getAttribute('aria-describedby')
        const popper = document.getElementById(describedBy)
        if (popper) {
          popper.style.display = 'inline-block'
          popper.style.position = 'static'
        }
      })
    })


    return {
      date,
      datePickerRef,
      container
    }
  }
}
</script>
<style lang="scss">
.custom-datepicker {
  box-shadow: none !important;
  border: none !important;
}

.custom-datepicker .el-picker-panel__body,
.custom-datepicker .el-date-range-picker__content {
  border: none !important;
  box-shadow: none !important;
  padding: 0.5rem;
}
</style>
