import { defineStore } from 'pinia'
import request from 'utils/request'

export const useImei = defineStore('imei', {
  state: () => ({
    imeiCheck: {
      isLoading: false,
      result: null,
      tempPayload: null
    },
    imeiCheckPrint: {
      isLoading: false,
      result: null,
      tempPayload: null
    }
  }),
  actions: {
    async $getImeiCheck(payload) {
      if (JSON.stringify(this.imeiCheck.tempPayload) === JSON.stringify(payload) && this.imeiCheck.result) {
        return
      }

      this.imeiCheck.tempPayload = payload

      const response = await request({
        url: `/v1/pos/order/product-check-imei`,
        method: 'post',
        data: {
          ...payload,
          _method: 'POST'
        }
      })

      this.imeiCheck.result = response
    },
    async $getImeiCheckPrint(payload, type = undefined /* bulk etc. */) {
      if (JSON.stringify(this.imeiCheckPrint.tempPayload) === JSON.stringify(payload) && this.imeiCheckPrint.result) {
        return
      }

      this.imeiCheckPrint.tempPayload = payload

      const response = await request({
        url: `/v1/pos/order/product-check-imei/${type ? 'print-' + type : 'print'}`,
        method: 'post',
        data: {
          ...payload,
          _method: 'POST'
        },
        responseType: 'arraybuffer'
      })

      this.imeiCheckPrint.result = response
    }
  }
})