<script setup>
import { ref, defineEmits } from 'vue'

import BulkSellInstantly from 'components/Inventory/BulkSellInstantly.vue'

const modal = ref(false)
const data = ref([])
const emits = defineEmits([
  'confirm'
])

const onComplete = () => emits('confirm')

const onClose = () => {
  modal.value = false
}

const onShow = (item) => {
  if (item) {
    modal.value = true
    data.value = Array.isArray(item) ? item : [item]
  }
}

</script>

<template>
  <div>
    <slot v-bind="{ onShow }"></slot>
    <BulkSellInstantly
      v-model:open="modal"
      :inventories="data"
      @complete="onComplete"
      @close="onClose"
    />
  </div>
</template>
