<script setup>
import { ref, defineProps, defineEmits } from 'vue'
import { useInventoryOthers } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'

import Select from 'components/Select.vue'

import Alert from 'bbUi/components/alert/alert.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'

const store =  useInventoryOthers()
const props = defineProps({
  inventories: {
    type: Array,
    default: () => []
  }
})
const emits = defineEmits(['submit'])
const modal = ref(false)
const location = ref({})

const onClose = () => {
  modal.value = false
  location.value = {}
}

const onShow = async () => {
  modal.value = true
  if (!store.locations?.length) {
    await tryCatchRequest(
      store.move.locations,
      () => store.$getMoveLocations()
    )
  }
}

const onSelectLocation = (e) => {
  location.value = store.locations.find(location => location.id === e)
}

const onProceed = async () => {
  await tryCatchRequest(
    store.move.locations,
    () => store.$setMoveInventory({
      selected_inventory_id: props.inventories.map(inventory => inventory.id).toString(),
      location_id: location.value.id
    }).then(() => {
      ElMessage({
        message: `${props.inventories.length} Inventory moved.`,
        type: 'success'
      })
      onClose()
      emits('submit')
    })
  )
}

</script>

<template>
  <div class="inline-block">
    <slot v-bind="{ onShow }" />
    <Teleport to="body">
      <Modal
        v-model="modal"
        @close="onClose">
        <template #header>
          <div class="w-full">
            <h5 class="modal__content-header-title font-medium">
              Move Inventory
            </h5>
          </div>
        </template>
        <template #default>
          <div v-loading="store.move.locations.isLoading">
            <div class="space-y-2">
              <p class="font-medium">
                Choose a location to move the selected inventory to
              </p>
              <Select
                :modelValue="location"
                :items="store.locations"
                by="id"
                name="name"
                no-border
                maxHeight="200px"
                :style="{
                  width: '100%',
                  boxShadow: 'unset'
                }"
                placeholder="Choose Location"
                @update:modelValue="onSelectLocation">
                <template #item="{ item }">
                  <div
                    :class="[
                      location.id === item.id && 'font-medium text-teal-04',
                      'text-teal-03',
                      'flex items-center gap-x-2'
                    ]">
                    {{ item.name }}
                    <span
                      :class="item.id === location.id ? 'opacity-100' : 'opacity-0'"
                      class="material-icons duration-300 transition-opacity ease-in-out">
                      check_circle
                    </span>
                  </div>
                </template>
              </Select>
            </div>
            <Alert color="orange" view="smooth" class="my-6 custom-p-1" dense>
              <div class="flex items-center h-full">
                <p class="text-lg">
                  <strong>
                    <span class="material-icons">info</span>
                    {{ props?.inventories?.length }} inventory lists selected.
                  </strong>
                  Before proccessing, please double-check the list.
                </p>
              </div>
            </Alert>
            <div class="w-full p-5 border border-grey-01 rounded-md">
              <Table
                v-model:data="props.inventories">
                <TableColumn prop="id" label="ID" width="80" />
                <TableColumn prop="item_name" label="Item">
                  <template #default="scope">
                    <router-link
                      :to="{
                        name: 'inventoryEdit',
                        params: { inventoryId: scope.row.id }
                      }"
                      class="font-semibold inline-block word-break">
                      {{ scope.row.item_name }}
                    </router-link>
                  </template>
                </TableColumn>
                <TableColumn prop="imeisn" label="IMEI/SN" />
              </Table>
            </div>
          </div>
        </template>
        <template #footer>
          <Button
            :loading="store.move.isLoading"
            :disabled="!props?.inventories?.length || typeof location?.id === undefined"
            color="orange"
            @click="onProceed">
            Proceed
          </Button>
        </template>
      </Modal>
    </Teleport>
  </div>
</template>
