<script setup>
import { defineProps, reactive } from 'vue'
import { useInventory } from 'store/pinia/inventory'

import Alert from 'bbUi/components/alert/alert.vue'
import Currency from 'components/Currency.vue'

const ALERT_TYPES = {
  default: 'closedInventoryAlertDefault',
  pending: 'closedInventoryAlertPending'
}

const store = useInventory()
const props = defineProps({
  alertType: {
    type: String,
    default: 'default'
  }
})

const alerts = reactive({
  default: localStorage.getItem(ALERT_TYPES[props.alertType]) == 1 ? false : true,
  pending: localStorage.getItem(ALERT_TYPES[props.alertType]) == 1 ? false : true
})

const onCloseAlert = () => {
  alerts[props.alertType] = false
  localStorage.setItem(ALERT_TYPES[props.alertType], '1')
}

</script>

<template>
  <div v-if="props.alertType === 'default'">
    <Alert
      v-if="alerts.default"
      color="orange"
      view="smooth"
      class="mb-5"
      dense>
      <div class="flex items-center h-full">
        After a buyback offer is marked completed, items will automatically be added to your available inventory.
      </div>
      <template #left>
        <span class="material-icons text-4xl">info</span>
      </template>
      <template #right>
        <span
          class="material-icons my-auto ml-auto cursor-pointer"
          @click="onCloseAlert">
          close
        </span>
      </template>
    </Alert>
  </div>
  <div v-if="props.alertType === 'pending'">
    <Alert
      v-if="store.inventory.statusId == '1' && alerts.pending"
      color="orange"
      view="smooth"
      dense>
      <div class="flex items-center h-full">
        <p class="text-md">Pending inventory is inventory that is currently on an invoice waiting to be paid.</p>
      </div>
      <template #left>
        <span class="material-icons text-4xl">info</span>
      </template>
      <template #right>
        <span
          class="material-icons my-auto ml-auto cursor-pointer"
          @click="onCloseAlert">
          close
        </span>
      </template>
    </Alert>
  </div>
  <div v-if="props.alertType === 'cost'">
    <Alert
      v-if="store.inventory.statusId == '0'"
      v-loading="store.cost.isLoading"
      color="teal"
      view="smooth"
      dense
      class="mb-5">
      <div
        v-if="store?.inventory?.result?.total_cost"
        class="flex items-center h-full">
        <p class="text-lg text-grey-04">
          Total available inventory cost
          <span class="font-semibold">
            <Currency :value="store.inventory.result.total_cost ?? 0" />
          </span>
        </p>
      </div>
      <template #left>
        <img
          src="@/assets/calculator.svg"
          alt="Icon - calculator"
          class="mr-5"
        >
      </template>
    </Alert>
  </div>
</template>