<script setup>
import { computed, defineExpose, ref } from 'vue'
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'
import { useUser } from 'composable/user'
import debounce from 'utils/debounce'
import { deepClone } from 'utils/deep-clone'
import { formatDatabase } from 'utils/date'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import FilterDropdown from 'components/FilterDropdown.vue'
import Pagination from 'components/Pagination.vue'
import DatePickerPanel from 'components/DatePickerClone/DatePickerPanel.vue'
import BDropdown from 'components/BDropdown/BDropdown.vue'

const store = useInventory()
const { dateByTimezone } = useUser()
const data = ref({
  date: [],
  startDate: null,
  endDate: null,
  dateAdded: [],
  dateSold: [],
  startDateAdded: null,
  endDateAdded: null,
  startDateSold: null,
  endDateSold: null,
  searchLocation: null,
  pageLocation: 1,
  selectedLocation: null,
  manufacturPage: {
    manufacturer: 1,
    model: 1,
    size: 1,
    network: 1,
    condition: 1
  },
  selectedManufactur: {
    manufacturer: [],
    model: [],
    size: [],
    network: [],
    condition: []
  }
})
const isDateShow = ref(false)
const filterDateRef = ref()
const filterOptions = ref([
  {
    name: 'Select Date',
    key: 'date',
    filter_key: 'date',
    count_selected: 0
  },
  {
    name: 'Location',
    key: 'location',
    filter_key: 'location',
    count_selected: 0
  },
  {
    name: 'Brand',
    key: 'manufacturer',
    filter_key: 'manufacturer',
    count_selected: 0
  },
  {
    name: 'Model',
    key: 'model',
    count_selected: 0,
    filter_key: 'manufacturer'
  },
  {
    name: 'Size',
    key: 'size',
    filter_key: 'manufacturer',
    count_selected: 0
  },
  {
    name: 'Network',
    key: 'network',
    filter_key: 'manufacturer',
    count_selected: 0
  },
  {
    name: 'Condition',
    key: 'condition',
    filter_key: 'manufacturer',
    count_selected: 0
  }
])
const disabledAppliedFilter = computed(() => {
  if (
    !data.value.selectedLocation?.id &&
    !data.value.selectedManufactur.manufacturer.length &&
    !data.value.selectedManufactur.model.length &&
    !data.value.selectedManufactur.size.length &&
    !data.value.selectedManufactur.network.length &&
    !data.value.selectedManufactur.condition.length &&
    !data.value.date?.length &&
    !data.value.dateAdded?.length &&
    !data.value.dateSold?.length
  ) return true

  return false
})

const onItemFilterClick = async (filter) => {
  if(!filter) return
  if(filter.key === 'date') {

    // console.log(key)
    // if (isDateShow.value) {
    //   isDateShow.value = false
    //   return
    // }

    // isDateShow.value = true
    // filterDateRef.value.focus()
  }
}

const onFetchLocation = async () => {
  await tryCatchRequest(
    store.filter.location,
    () => store.$getFilterLocation({
      search: data.value.searchLocation,
      page: data.value.pageLocation
    })
  )
  if (!data.value.selectedLocation)
    data.value.selectedLocation = store.filter?.location?.result?.data[0] ?? null
}

const onFetchManufacturer = async (key, modelOnly = false) => {
  await tryCatchRequest(
    modelOnly ? store.filter.model : store.filter[key],
    () => store.$getFilterManufacturer({
      manufacturer: data.value.selectedManufactur.manufacturer.join(','),
      model: data.value.selectedManufactur.model.join(','),
      size: data.value.selectedManufactur.size.join(','),
      network: data.value.selectedManufactur.network.join(','),
      condition: data.value.selectedManufactur.condition.join(',')
    }, modelOnly)
  ) 
}

const onSearchLocation = debounce(async () => {
  data.value.pageLocation = 1
  await onFetchLocation()
}, 300)

const onChangePageLocation = async (value) => {
  data.value.pageLocation = value
  await onFetchLocation()
}

const onChangePageManufature = (value, key) => {
  data.value.manufacturPage[key] = value
}

const onSelectLocation = (val, callback = () => {}) => {
  data.value.selectedLocation = val
  const index = filterOptions.value.findIndex(i => i.key === 'location')
  const item = filterOptions.value[index]
  if (!!item) filterOptions.value[index].count_selected = data.value.selectedLocation.id ? 1 : 0
  typeof callback === 'function' && callback()
}

const onFetchFilters = async() => {
  await Promise.all([
    onFetchLocation(),
    onFetchManufacturer('all')
  ])
}

const onUpdateDate = (e, key, callback = () => {}) => {
  if (key === 'added') {
    data.value.startDateAdded = formatDatabase(new Date(data.value.dateAdded[0]))
    data.value.endDateAdded = formatDatabase(new Date(data.value.dateAdded[1]))
  } else if (key === 'sold') {
    data.value.startDateSold = formatDatabase(new Date(data.value.dateSold[0]))
    data.value.endDateSold = formatDatabase(new Date(data.value.dateSold[1]))
  } else {
    data.value.startDate =formatDatabase(new Date(data.value.date[0]))
    data.value.endDate = formatDatabase(new Date(data.value.date[1]))
  }
  callback()
}

const onItemCollapse = async (collapse) => {
  isDateShow.value = false
  if (collapse) {
    await onFetchFilters()
  }
}

const onCheckItem = async (key) =>{
  const index = filterOptions.value.findIndex(i => i.key === key)
  const item = filterOptions.value[index]
  if (!!item) filterOptions.value[index].count_selected = data.value.selectedManufactur[key].length
  if (key !== 'model') await onFetchManufacturer('model', true)
}

const onApplyFilter = () => {
  store.filterTags = deepClone({
    ...deepClone(data.value.selectedManufactur),
    location: deepClone(data.value.selectedLocation),
    ...(data.value.startDateSold && !!data.value.endDateSold && { sold: [data.value.startDateSold, data.value.endDateSold] }),
    ...(data.value.startDateAdded && data.value.endDateAdded && { added: [data.value.startDateAdded, data.value.endDateAdded] }),
    ...(data.value.startDate && data.value.endDate && { date: [data.value.startDate, data.value.endDate] })
  })
}

const capitalize = (str) => str.charAt(0).toUpperCase() + str.slice(1)

const onResetDateRange = (prefix) => {
  data.value[`${prefix}`] = null
  data.value[`start${capitalize(prefix)}`] = null
  data.value[`end${capitalize(prefix)}`] = null
}

const onResetManufacturers = () => {
  data.value.selectedManufactur = {
    manufacturer: [],
    model: [],
    size: [],
    network: [],
    condition: []
  }
}

const onResetAllDates = () => {
  ['date', 'dateAdded', 'dateSold'].forEach(key => onResetDateRange(key))
}

const onResetFilter = (key = undefined) => {
  if (typeof key === 'string') {
    if (key === 'location') {
      data.value.selectedLocation = store.filter?.location?.result?.data[0] ?? null
    } else {
      if (['date', 'added', 'sold'].includes(key)) {
        const dateKeyMap = {
          date: 'date',
          added: 'dateAdded',
          sold: 'dateSold'
        }
        onResetDateRange(dateKeyMap[key])
      }
      data.value.selectedManufactur[key] = []
    }

    filterOptions.value = filterOptions.value.map(i => ({
      ...i,
      count_selected: i.key === key ? 0 : i.count_selected
    }))

    onApplyFilter()
  } else {
    onResetManufacturers()
    data.value.selectedLocation = store.filter?.location?.result?.data[0] ?? null
    onResetAllDates()
    
    filterOptions.value = filterOptions.value.map(i => ({
      ...i,
      count_selected: 0
    }))

    store.filterTags = deepClone({})
  }
}

store.getTimeZone(dateByTimezone.value)

defineExpose({
  data,
  onResetFilter
})
</script>

<template>
  <div>
    <FilterDropdown
      :loading="store.inventory.isLoading"
      :expand-on-hover="false"
      :filters="filterOptions"
      :reset-disabled="disabledAppliedFilter"
      @item-click="onItemFilterClick"
      @collapse="onItemCollapse"
      @apply-filter="onApplyFilter"
      @reset-filter="onResetFilter">
      <template #label="{ item }">
        <div v-loading="store?.filter[item.meta.key]?.isLoading || store?.filter?.all?.isLoading">
          <p class="text-grey-04 flex items-center gap-x-1 max-w-xs">
            {{ item.name }}
            <template v-if="item.meta.count_selected > 0">
              <span>
                ({{ item.meta.count_selected }})
              </span>
            </template>
            <template v-if="
              item.meta.key === 'date' &&
              (!!data.date?.length || !!data.dateSold?.length || !!data.dateAdded?.length)">
              <span class="material-icons">
                check
              </span>
            </template>
          </p>
        </div>
      </template>
      <template #dropdown="{ filter, collapse }">
        <div class="flex flex-col p-3">
          <!-- Date -->
            <div v-if="filter.key === 'date'">
              <template v-if="store.inventory.statusId == 2">
                <BDropdown
                  bottom
                  block
                  :min-width="200"
                  :offset="10"
                  :max-height="540">
                  <div class="bg-grey-01 hover:bg-opacity-50 bg-opacity-0 rounded-md">
                    <div class="px-6 py-4 cursor-pointer">
                      Date Added
                    </div>
                  </div>
                  <template #dropdown="{ collapse: grandChildCollapseAdded }">
                    <DatePickerPanel
                      v-model="data.dateAdded"
                      :options="store.dateOptions"
                      @update:modelValue="onUpdateDate($event, 'added', grandChildCollapseAdded)"
                    />
                  </template>
                </BDropdown>
                <BDropdown
                  bottom
                  block
                  :min-width="200"
                  :offset="10"
                  :max-height="540">
                  <div class="bg-grey-01 hover:bg-opacity-50 bg-opacity-0 rounded-md">
                    <div class="px-6 py-4 cursor-pointer">
                      Date Sold
                    </div>
                  </div>
                  <template #dropdown="{ collapse: grandChildCollapseSold }">
                    <DatePickerPanel
                      v-model="data.dateSold"
                      :options="store.dateOptions"
                      @update:modelValue="onUpdateDate($event, 'sold', grandChildCollapseSold)"
                    />
                  </template>
                </BDropdown>
              </template>
              <template v-else>
                <DatePickerPanel
                  v-model="data.date"
                  :options="store.dateOptions"
                  @update:modelValue="onUpdateDate($event, 'date', collapse)"
                />
              </template>
            </div>
          <!-- Date -->
          <!-- Location -->
            <div
              v-if="filter.key === 'location'"
              class="px-2 py-2 w-full max-w-xs overflow-x-hidden">
              <Input
                v-model="data.searchLocation"
                block
                icon
                placeholder="Search location"
                @input="onSearchLocation">
                <template #icon>
                  <span class="material-icons">search</span>
                </template>
              </Input>
              <div v-loading="store.filter.location.isLoading">
                <div
                  v-if="store.filter?.location?.result?.data?.length"
                  class="text-teal-04">
                  <div
                    v-for="location in store?.filter?.location?.result?.data"
                    :key="location.id"
                    class="flex items-center py-3 px-4 my-2 hover:bg-teal-lighten cursor-pointer select-none focus:text-teal-04 focus:font-semibold rounded-lg gap-x-1"
                    tabindex="0"
                    @click="onSelectLocation(location, collapse)">
                    <span :class="location.id === data.selectedLocation?.id && 'font-medium'">
                      {{ location.name }}
                    </span>
                    <span
                      :class="[
                        'material-icons',
                        'text-lg',
                        'text-teal-04',
                        'transition-opacity',
                        'ease-in-out duration-300',
                        location.id === data.selectedLocation?.id ? 'opacity-100' : 'opacity-0'
                        // location.is_selected_location
                        //   ? $style['location-point--selected']
                        //   : $style['location-point']
                      ]">
                      check_circle
                    </span>
                  </div>
                  <Pagination
                    :page="data.pageLocation"
                    @update:page="onChangePageLocation"
                    :data="store.filter.location.result"
                    small
                    hide-show-entries
                  />
                </div>
              </div>
            </div>
          <!-- Location -->
          <!-- Brand/Manufaturer -->
            <div
              v-else-if="filter.key !== 'date' && store.filter[filter.key]?.result"
              class="px-2 py-2 w-full max-w-md overflow-x-hidden">
              <div v-loading="store.filter[filter.key]?.isLoading">
                <div
                  v-if="store.filter[filter.key]?.result?.length"
                  class="text-teal-04 space-y-2">
                  <div class="space-y-2 max-h-96 overflow-y-auto">
                    <div
                      v-for="(item, n) in store.filter[filter.key]?.result[data.manufacturPage[filter.key] - 1]"
                      :key="n">
                      <Checkbox
                        v-model="data.selectedManufactur[filter.key]"
                        color="teal"
                        :value="item"
                        @change="onCheckItem(filter.key)">
                        <span
                          :class="
                            data.selectedManufactur[filter.key]?.includes(item)
                              ? 'font-medium'
                              : 'font-normal'">
                          {{ filter.key === 'model' ? item.name : item }}
                        </span>
                      </Checkbox>
                    </div>
                  </div>
                  <Pagination
                    :page="data.manufacturPage[filter.key]"
                    v-bind="{
                      data: {
                        last_page: store.filter[filter.key]?.result?.length,
                        current_page: data.manufacturPage[filter.key]
                      }
                    }"
                    small
                    hide-show-entries
                    hide-detail-page
                    @update:page="onChangePageManufature($event, filter.key)"
                  />
                </div>
                <div v-else class="h-16 flex items-center justify-center">
                  <template v-if="filter.key === 'model' && !data?.selectedManufactur?.manufacturer?.length">
                    {{ 'Check brand first' }}
                  </template>
                  <template v-else>
                    {{ 'Nothing to show' }}
                  </template>
                </div>
              </div>
            </div>
          <!-- Brand/Manufaturer -->
        </div>
      </template>
    </FilterDropdown>
  </div>
</template>
