<template>
  <div>
    <Select
      v-model="selectedPaymentId"
      :items="items"
      name="name"
      by="id"
      label="Choose Payout Method"
      placeholder="Choose Payout Method"
      class="w-full mb-4"
      :view="isInvalid ? 'danger' : 'default'"
    />
    <template v-if="selectedPaymentData.is_custom">
      <component
        ref="refComponent"
        v-model:data="data"
        :payout="selectedPaymentData"
        :is="selectedPaymentData.component"
      ></component>
    </template>
    <template v-else>
      <component
        ref="refComponent"
        v-bind="bind"
        v-model:data="data"
        :is="selectedPaymentData.component"
      ></component>
    </template>
    <div
      v-if="selectedPaymentData.desc"
      class="bg-green-01 px-6 py-4 rounded-lg font-normal text-green-04 mb-2"
    >
      {{ selectedPaymentData.desc }}
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, computed, watch } from 'vue'
import Select from 'bbUi/components/select/select.vue'
import AmazonGiftCard from 'components/PaymentMethod/AmazonGiftCard.vue'
import BankTransfer from 'components/PaymentMethod/BankTransfer.vue'
import PaperCheck from 'components/PaymentMethod/PaperCheck.vue'
import ExpressCheck from 'components/PaymentMethod/PaperCheck.vue'
import PayPal from 'components/PaymentMethod/PayPal.vue'
import Swish from 'components/PaymentMethod/Swish.vue'
import SquareCash from 'components/PaymentMethod/SquareCash.vue'
import Venmo from 'components/PaymentMethod/Venmo.vue'
import Coinbase from 'components/PaymentMethod/Coinbase.vue'
import Zelle from 'components/PaymentMethod/Zelle.vue'
import CashApp from 'components/PaymentMethod/CashApp.vue'
import ApplePay from 'components/PaymentMethod/ApplePay.vue'
import GooglePay from 'components/PaymentMethod/GooglePay.vue'
import FacebookPay from 'components/PaymentMethod/FacebookPay.vue'
import Cash from 'components/PaymentMethod/Cash.vue'
import ChooseLater from 'components/PaymentMethod/ChooseLater.vue'
import StoreCredit from 'components/PaymentMethod/StoreCredit.vue'
import VirtualDebitCard from 'components/PaymentMethod/VirtualDebitCard.vue'
import GiftCard from 'components/PaymentMethod/GiftCard.vue'
import InteraceTransfer from 'components/PaymentMethod/InteracTransfer.vue'
import Donate from 'components/PaymentMethod/Donate.vue'
import Bill from 'components/PaymentMethod/Bill.vue'
import ECheck from 'components/PaymentMethod/Echeck.vue'
import Rebate from 'components/PaymentMethod/Rebate.vue'
import Revolut from 'components/PaymentMethod/Revolut.vue'
import Custom from 'components/PaymentMethod/Custom.vue'
import Default from 'components/PaymentMethod/Default.vue'
import Button from 'bbUi/components/button/button.vue'

import { PAYMENT_ID } from '@/constants/payment.js'

export default defineComponent({
  components: {
    Select,
    AmazonGiftCard,
    BankTransfer,
    PaperCheck,
    ExpressCheck,
    PayPal,
    Swish,
    SquareCash,
    Venmo,
    Coinbase,
    Cash,
    Zelle,
    CashApp,
    ApplePay,
    GooglePay,
    FacebookPay,
    StoreCredit,
    ChooseLater,
    VirtualDebitCard,
    GiftCard,
    InteraceTransfer,
    Donate,
    Bill,
    ECheck,
    Rebate,
    Revolut,
    Custom,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({})
    },
    items: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:data'],
  setup(props, { emit }) {
    const refComponent = ref(null)
    const selectedPaymentId = ref(null)
    const isInvalid = ref(false)

    const isBankTransferPayment = computed(() => selectedPaymentId.value === PAYMENT_ID.BANK_TRANSFER)
    const selectedPaymentData = computed(
      () =>
        props.items.find(
          (payment) => payment.id === selectedPaymentId.value
        ) || {
          id: null,
          name: null,
          component: null
        }
    )
    const data = computed({
      set: (newVal) => {
        emit('update:data', {
          payment_method: selectedPaymentData.value.name,
          ...newVal
        })
      },
      get: () => {
        if (isBankTransferPayment.value) {
          return { ...props.data, is_new_bank: true }
        }
        return { ...props.data }
      }
    })
    const bind = computed(() => {
      const bindValues = {}
      if (isBankTransferPayment.value) {
        bindValues.defaultAttributes =
          selectedPaymentData.value?.attributes || []
        bindValues.list = Array.isArray(selectedPaymentData.value?.default_value) ? selectedPaymentData.value?.default_value : []
      }
      return bindValues
    })
    const validate = () => {
      if (!selectedPaymentId.value) {
        isInvalid.value = true
        return false
      }

      if (!refComponent.value?.validate) return false
      return refComponent.value.validate()
    }

    watch(
      () => props.data,
      value => {
        if (value && value.payment_method && !selectedPaymentId.value) {
          const item = props.items.find(item => item.name === value.payment_method)
          selectedPaymentId.value = item && item.id ? item.id : null
        }
      },
      { immediate: true }
    )

    watch(
      () => selectedPaymentId.value,
      () => {
        isInvalid.value = false
        const temps = {}
        if (selectedPaymentData.value?.default_value && selectedPaymentData.value?.attributes?.length) {
          selectedPaymentData.value.attributes.forEach(attribute => {
            if ('billing_address1' === attribute) {
              temps.billing_address_text = selectedPaymentData.value.default_value.billing_address_text
              temps.billing_address1 = selectedPaymentData.value.default_value.billing_address1
            } else temps[attribute] = selectedPaymentData.value.default_value[attribute]
          })
        }
        emit('update:data', {
          ...temps,
          payment_method: selectedPaymentData.value.name
        })
      }
    )

    return {
      refComponent,
      isInvalid,
      selectedPaymentId,
      selectedPaymentData,
      data,
      bind,
      validate
    }
  }
})
</script>
