<script setup>
import { nextTick, reactive, ref, computed } from 'vue'
import { useRouter } from 'vue-router'
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'

import Button from 'bbUi/components/button/button.vue'

import BTable from 'components/BTable/BTable.vue'
import Currency from 'components/Currency.vue'
import Pagination from 'components/Pagination.vue'

import InventoryStatusSelect from './InventoryStatusSelect.vue'
import InventoryActions from './InventoryActions.vue'
import InventoryMoveInventory from './InventoryMoveInventory.vue'
import InventoryImeiPhoneCheck from './InventoryImeiPhoneCheck.vue'
import InventorySellInstantly from './InventorySellInstantly.vue'

const store = useInventory()
const router = useRouter()
const selectedInventories = ref([])
const selectedInventory = ref()
const modals = reactive({
  actions: false,
  phoneCheck: false
})
const actionType = ref()
const loadingBulkSellInstantly = ref(false)
const disabledBulkSellInstantly = computed(() => {
  return selectedInventories.value.filter((inventory) =>
    !inventory.price_sharing && inventory.deeplink?.link === '#'
  ).length > 0
})
const bulkSellInstantlyAvailable = computed(() => {
  const byStatusID = store.inventory.statusId == 0

  if (!byStatusID) return false

  const byPriceSharing = selectedInventories.value.filter((inventory) =>
    !!inventory.price_sharing && inventory.deeplink?.link !== '#'
  ).length > 0

  if (!byPriceSharing) return false

  return true
})

const onTableRowClick = async () => {
  await nextTick(() => {
    router.push({ 
      name: 'inventoryEdit', 
      params: { 
        inventoryId: itemRow.id 
      }
    })
  })
}

const onChangeSelected = async (items) => {
  if (store.inventory.statusId != 0) return

  const unLoad = [...selectedInventories.value].filter(i => !Object.keys(i.deeplink)?.length)

  if (unLoad.length) {
    loadingBulkSellInstantly.value = true
    await Promise.all(
      unLoad.map(async (i) => {
        const indexInventory = [...store.inventory?.result?.data].findIndex(j => j.id === i.id)
        if (store.inventory.result.data[indexInventory]) {
          store.inventory.result.data[indexInventory] = await onLoadDeeplink(store.inventory?.result?.data[indexInventory])
        }
      })
    )
    loadingBulkSellInstantly.value = false
  }
}

const onLoadDeeplink = async (e, bulk = false) => {
  if (store.inventory.statusId != 0) return

  if (Object.keys(e.deeplink).length) return

  e.loading = true
  await store.$getInventoryDeepLink({
    id: e.id
  }).then((res) => {
    e.loading = false
    e.deeplink = {
      ...res,
      is_valid: res.link !== '#' && res.link != 0 && e.status != 0 && store.inventory.statusId == 0
    }
  })
  .catch(() => {
    e.loading = false
    e.deeplink = {
      link: '#',
      price: 0,
      is_valid: false
    }
  })
  return e
}

const onSingleCheck = async (row, callback = async () => {}) => {
  row.loading = true
  await callback()
  row.loading = false
}

const onBulkCheck = async (callback = async () => {}) => {
  const available = await callback()
  if (available) {
    await tryCatchRequest(
      store.inventory,
      () => store.$getInventory()
    )
  }
}

const onSellInstantly = async () => {
  await tryCatchRequest(
    store.inventory,
    () => store.$getInventory()
  )
}

const onOpenModalAction = async (key, item) => {
  if (key && modals !== undefined && item) {
    modals.actions = true
    actionType.value = key
    selectedInventory.value = item
  }
}

const onActionTable = async () => {
  await tryCatchRequest(
    store.inventory,
    () => store.$getInventory()
  )
}

const onChangeStatus = (e, item) => {
  item.status = e
  return item
}

const onChangePage = async (e) => {
  if (e != store.inventory.page) {
    store.inventory.page = e
    await tryCatchRequest(
      store.inventory,
      () => store.$getInventory()
    )
  }
}
const onchangeLimitPage = async (e) => {
  if (e != store.inventory.limit) {
    if (store.inventory.page != 1) store.inventory.page = 1
    store.inventory.limit = e
    await tryCatchRequest(
      store.inventory,
      () => store.$getInventory()
    )
  }
}

const onInventoryMoved = async () => {
  if (store.inventory.page != 1) store.inventory.page = 1
    await tryCatchRequest(
      store.inventory,
      () => store.$getInventory()
    )
}

</script>

<template>
  <div v-loading="store.inventory.isLoading" style="min-height: 100px">
    <div v-if="store.inventory?.result" class="space-y-4">
      <div class="flex items-center gap-x-4">
        <template v-if="!!selectedInventories?.length">
          <InventoryImeiPhoneCheck
            :data="selectedInventories">
            <template #default="{ onCheck }">
              <Button
                color="teal"
                dense
                @click="onBulkCheck(onCheck)">
                Import from Phonecheck
              </Button>
            </template>
          </InventoryImeiPhoneCheck>
          <InventoryMoveInventory
            :inventories="selectedInventories"
            @submit="onInventoryMoved">
            <template #default="{ onShow }">
              <Button
                color="teal"
                dense
                @click="onShow">
                Move Inventory
              </Button>
            </template>
          </InventoryMoveInventory>
        </template>
        <template v-if="!!selectedInventories.length && bulkSellInstantlyAvailable">
          <InventorySellInstantly>
            <template #default="{ onShow }">
              <Tooltip
                placement="right"
                content="There are some inventory items that are not able to be sold instantly"
                :disabled="!disabledBulkSellInstantly">
                <div>
                  <Button
                    :disabled="disabledBulkSellInstantly"
                    :loading="loadingBulkSellInstantly"
                    view="smooth"
                    color="orange"
                    dense
                    @click="onShow(selectedInventories)">
                    Bulk Sell Instantly
                  </Button>
                </div>
              </Tooltip>
            </template>
          </InventorySellInstantly>
        </template>
      </div>
      <BTable
        v-model:selected="selectedInventories"
        :items="store.inventory?.result?.data || []"
        :headers="store.inventory?.result.header_table || []"
        :clickable-row="true"
        selectable
        auto-width
        item-value="id"
        :intersect="store.inventory.statusId == 0"
        :observer-table="onLoadDeeplink"
        :more-actions="['delete', 'reset', 'restore']"
        @update:selected="onChangeSelected">
        <template
          #column="{
            row,
            column,
            key,
            preventRowClick: onRowClick,
            resetRowClick: onResetRowClick
          }">
          <td
            v-if="key === 'item_name'"
            @mouseenter="onRowClick"
            @mouseleave="onResetRowClick">
            <router-link
              :to="{
                name: 'inventoryEdit',
                params: { inventoryId: row.id }
              }"
              class="font-semibold inline-block word-break">
              {{ column }}
            </router-link>
          </td>
          <td v-else-if="key === 'age'">
            {{ [column || '0', 'Day(s)'].join(' ') }}
          </td>
          <td
            v-else-if="key === 'imeisn'"
            @mouseenter="onRowClick"
            @mouseleave="onResetRowClick">
            <InventoryImeiPhoneCheck
              v-if="store.enablePhoneCheck && row.phonecheck_status == 'exists'"
              :data="row">
              <template #default="{ onCheck }">
                <span
                  class="cursor-pointer text-blue-03 hover:underline"
                  @click="onSingleCheck(row, onCheck)">
                  <div>
                    <img
                      src="https://d18hvo5uozp8li.cloudfront.net/public/images/phonecheck.svg"
                      alt=""
                      class="mr-3 float-left h-6"
                    />
                  </div>
                  {{ column }}
                </span>
              </template>
            </InventoryImeiPhoneCheck>
            <span v-else>
              {{ column }}
            </span>
          </td>
          <td v-else-if="key === 'cost'">
            <Currency :value="row.cost || 0" />
          </td>
          <td v-else-if="key === 'price'">
            <Currency :value="row.price || 0" />
          </td>
          <td v-else-if="key === 'profit'">
            <Currency :value="row.profit || 0" />
          </td>
          <td v-else-if="key === 'sold_price'">
            <Currency :value="row.sold_price || 0" />
          </td>
          <td
            v-else-if="key === 'invoice'"
            @mouseenter="onRowClick"
            @mouseleave="onResetRowClick">
            <router-link
              v-if="row.invoice"
              class="font-semibold"
              :to="{
                name: 'invoiceDetail',
                params: { buybackId: row.invoice }
              }">
              {{ column }}
              <i class="fas fa-external-link-alt ml-1"></i>
            </router-link>
          </td>
          <td
            v-else-if="key === 'source'"
            @mouseenter="onRowClick"
            @mouseleave="onResetRowClick">
            <a
              v-if="row.source_linkable !== 0"
              :href="row.source_link">
              {{ row.source || '-' }}
              <i class="fas fa-external-link-alt ml-1"></i>
            </a>
            <span v-else>
              {{ row.source || '-' }}
            </span>
          </td>
          <td
            v-else-if="key === 'status'"
            @mouseenter="onRowClick"
            @mouseleave="onResetRowClick">
            <InventoryStatusSelect
              :item="row"
              @change="onChangeStatus($event, row)"
            />
          </td>
          <td
            v-else-if="key === 'deeplink'"
            @mouseenter="preventRowClick"
            @mouseleave="resetRowClick">
            <InventorySellInstantly
              v-if="row.deeplink.is_valid"
              @confirm="onSellInstantly">
              <template #default="{ onShow }">
                <Button
                  color="teal"
                  @click="onShow(row)">
                  <p>
                    Sell instantly for {{ `${row.deeplink?.currency_symbol || '$'}${row.deeplink?.price || 0}` }}
                  </p>
                </Button>
              </template>
            </InventorySellInstantly>
          </td>
          <td v-else>{{ column || '-' }}</td>
        </template>

        <template #actionDelete="{ row }">
          <div
            v-if="row.status_id === 0 && !row.deleted_at"
            class="flex items-center gap-x-2 px-4 py-2"
            @click="onOpenModalAction('delete', row)">
            <span class="material-icons text-red-04">delete</span>
            <span class="text-sm">Delete</span>
          </div>
        </template>
        <template #actionReset="{ row }">
          <div
            v-if="row.deleted_at"
            class="flex items-center gap-x-2 px-4 py-2"
            @click="onOpenModalAction('reset', row)">
            <span class="material-icons text-green-04">replay</span>
            <span class="text-sm">Reset</span>
          </div>
        </template>
        <template #actionRestore="{ row }">
          <div
            v-if="row.status_id === 2"
            class="flex items-center gap-x-2 px-4 py-2"
            @click="onOpenModalAction('restore', row)">
            <span class="material-icons">settings_backup_restore</span>
            <span class="text-sm">Restore</span>
          </div>
        </template>
      </BTable>
      <Pagination
        v-model:page="store.inventory.result.current_page"
        v-model:limit="store.inventory.result.per_page"
        :data="store.inventory.result"
        @update:page="onChangePage"
        @update:limit="onchangeLimitPage"
      />
      <InventoryActions
        v-model="modals.actions"
        :data="selectedInventory"
        :action-type="actionType"
        @deleted="onActionTable"
        @reset="onActionTable"
        @restore="onActionTable"
      />
    </div>
  </div>
</template>