<script setup>
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'
import debounce from 'utils/debounce'

import Input from 'bbUi/components/input/input.vue'

const store = useInventory()

const onInput = debounce(async () => {
  await tryCatchRequest(
    store.inventory,
    () => store.$getInventory()
  )
}, 300)

</script>

<template>
  <Input
    v-model="store.inventory.search"
    placeholder="Search Everything"
    class="w-full"
    icon
    @input="onInput">
    <template #icon>
      <span class="material-icons">search</span>
    </template>
  </Input>
</template>
