<script setup>
import { defineProps, ref, computed } from 'vue'
import { useInventoryCrud } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'

import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

const store = useInventoryCrud()

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false
  },
  actionType: {
    type: String,
    default: 'delete'
  },
  data: {
    type: Object,
    default: undefined
  }
})

const emits = defineEmits([
  'deleted',
  'reset',
  'restore',
  'canceled',
  'update:modelValue'
])

const modal = computed({
  get: () => props.modelValue,
  set: (value) => emits('update:modelValue', value)
})
const notes = ref('')

const onCancel = () => {
  modal.value = false
  notes.value = ''
}

const onDelete = async () => {
  await tryCatchRequest(
    store.delete,
    () => store.$updateNotes({
      id: props.data.id,
      data: { notes: notes.value }
    })
  )
  await tryCatchRequest(
    store.delete,
    () => store.$onDelete(props.data.id).then(() => {
      onCancel()
      emits('deleted')
    })
  )
}

const onRestore = async () => {
  await tryCatchRequest(
    store.delete,
    () => store.$onRestore(props.data.id).then(() => {
      onCancel()
      emits('restore')
    })
  )
}

const onReset = async () => {
  await tryCatchRequest(
    store.delete,
    () => store.$onReset(props.data.id).then(() => {
      onCancel()
      emits('reset')
    })
  )
}

</script>

<template>
  <Teleport to="body">
    <Modal
      size="xs"
      v-model="modal"
      title="Delete Inventory"
      hide-header
      hide-footer
      @close="onCancel">
      <div
        v-if="actionType === 'delete'"
        class="py-10">
        <img
          class="mb-10 mx-auto"
          src="@/assets/icon/attention.svg"
          alt="Success Icon"
        />
        <div class="mt-8 mb-10">
          <h5 class="mb-4 text-center font-medium">
            Delete this inventory?
          </h5>
          <p class="text-center mb-6">
            Once you confirm, this inventory will be move to delete menu
          </p>
          <label class="font-medium">Additional Notes</label>
          <Input
            v-model="notes"
            type="textarea"
            block
          />
        </div>
        <div class="flex items-center justify-end w-full gap-x-3">
          <Button
            view="outlined"
            color="teal"
            class="bg-white"
            :disabled="store.delete.isLoading"
            @click="onCancel">
            Cancel
          </Button>
          <Button
            view="smooth"
            color="red"
            :disabled="!notes?.length"
            :loading="store.delete.isLoading"
            @click="onDelete">
            Yes, Delete
          </Button>
        </div>
      </div>
      <div
        v-if="actionType === 'reset'"
        class="text-center py-10">
        <img
          class="mb-10 mx-auto"
          src="@/assets/icon/attention.svg"
          alt="Success Icon">
        <div class="mt-8 mb-10">
          <h5 class="mb-4 font-medium">Warning?</h5>
          <p>
            Resetting inventory will move it back into the available status and reset any integrations.
          </p>
        </div>
        <Button
          view="smooth"
          color="teal"
          class="mr-3"
          :loading="store.reset.isLoading"
          @click="onReset">
          Yes, Reset
        </Button>
        <Button
          view="outlined"
          color="teal"
          class="bg-white"
          :disabled="store.reset.isLoading"
          @click="onCancel">
          Cancel
        </Button>
      </div>
      <div
        v-if="actionType === 'restore'"
        class="text-center py-10">
        <img class="mb-10 mx-auto" src="@/assets/icon/attention.svg" alt="Success Icon">
        <div class="mt-8 mb-10">
          <h5 class="mb-4 font-medium">Attention?</h5>
          <p>Are you sure want to restore this inventory?</p>
        </div>
        <Button
          view="smooth"
          color="teal"
          class="mr-3"
          :loading="store.restore.isLoading"
          @click="onRestore">
          Yes, Restore
        </Button>
        <Button
          view="outlined"
          color="teal"
          class="bg-white"
          :disabled="store.restore.isLoading"
          @click="onCancel">
          Cancel
        </Button>
      </div>
    </Modal>
  </Teleport>
</template>
