import { defineStore } from 'pinia'
import request from 'utils/request'
import { fileFormat } from 'utils/format'
import { anchorDownload } from 'utils/element'
// import date from 'utils/date'
import globalStore from 'store'
import { INVENTORY } from '@/constants/tabs'
import { useImei } from './buyback/imei'

export const useInventory = defineStore('inventory', {
  state: () => ({
    _tabs: INVENTORY,
    cost: {
      isLoading: false
    },
    inventory: {
      isLoading: false,
      statusId: null,
      search: null,
      result: null,
      page: 1,
      limit: 10,
      controller: null
    },
    inventoryExport: {
      params: undefined,
      isLoading: false
    },
    filter: {
      all: {
        isLoading: false
      },
      location: {
        isLoading: false,
        result: null,
        controller: null,
        tempParam: null
      },
      manufacturer: {
        isLoading: false,
        result: null,
        controller: null,
        tempParam: null
      },
      model: {
        isLoading: false,
        result: null,
        controller: null
      },
      size: {
        isLoading: false,
        result: null,
      },
      network: {
        isLoading: false,
        result: null,
      },
      condition: {
        isLoading: false,
        result: null
      }
    },
    filterTags: {},
    status: {
      isLoading: false
    },
    _timeZone: null,
    _user: null
  }),
  getters: {
    user(state) {
      return state._user
    },
    enablePhoneCheck(state) {
      return state._user?.is_integration_phonecheck_enable === 1 ?? false
    },
    tabs(state) {
      return state._tabs.map(i => ({
        id: i.id,
        name: i.short_name,
        to: {
          name: 'inventoryTab',
          params: {
            statusId: `${i.id}`
          }
        }
      }))
    },
    dateOptions(state) {
      return ({
        disabledDate(time) {
          return time.getTime() > state._timeZone.getTime()
        },
        // shortcuts: [
        //   {
        //     text: 'A Week Ago',
        //     value: [new Date(state._timeZone.getTime() - date(state._timeZone).weekAgo), state._timeZone]
        //   },
        //   {
        //     text: 'A Month Ago',
        //     value: [new Date(state._timeZone.getTime() - date(state._timeZone).monthAgo), state._timeZone]
        //   },
        //   {
        //     text: '2 Month Ago',
        //     value: [new Date(state._timeZone.getTime() - date(state._timeZone).monthAgo * 2), state._timeZone]
        //   },
        //   {
        //     text: '3 Month Ago',
        //     value: [new Date(state._timeZone.getTime() - date(state._timeZone).monthAgo * 3), state._timeZone]
        //   }
        // ]
      })
    }
  },
  actions: {
    getPaginateArray(array, itemsPerPage) {
      const origin = array // .filter(i => i.trim() !== '')
      const result = [];
      for (let i = 0; i < origin.length; i += itemsPerPage) {
        result.push(origin.slice(i, i + itemsPerPage))
      }
      return result
    },
    getStatusId(payload) {
      this.inventory.statusId = payload !== 'all' ? Number(payload) : 'all'
    },
    getTimeZone(payload) {
      this._timeZone = payload
    },
    async $getDataUser() {
      this._user = globalStore.state.user.me
    },
    async $getInventory(payload) {
      this.cost.isLoading = true

      try {
        const [from, to] = (this.filterTags?.added ?? this.filterTags?.date) ?? []
        const [sold_at_from, sold_at_to] = this.filterTags?.sold ?? []
        const payload = {
          status: this.inventory.statusId,
          search: this.inventory.search,
          page: this.inventory.page,
          limit: this.inventory.limit,
          store_location_id: this.filterTags?.location?.id ?? null,
          brand: this.filterTags?.manufacturer?.join(',') ?? null,
          model: this.filterTags?.model?.join(',') ?? null,
          network: this.filterTags?.network?.join(',') ?? null,
          size: this.filterTags?.size?.join(',') ?? null,
          condition: this.filterTags?.condition?.join(',') ?? null,
          from,
          to,
          sold_at_from,
          sold_at_to
        }
        this.inventoryExport.params = payload
        const response = await request({
          url: '/v2/pos/inventory/all',
          params: payload,
          method: 'get',
          signal: this.inventory?.controller?.signal
        })
        this.inventory.result = {
          ...response,
          header_table: [...response.header_table, { column_name: '', key: 'deeplink' }],
          data: response.data.map(i => ({
            ...i,
            loading: false,
            deeplink: {}
          }))
        }
      } catch (e) {
        console.error('Failed to fetch inventory', e)
      } finally {
        this.cost.isLoading = false
      }
    },
    async $getExport(payload, name) {
      const response = await request({
        url: '/v1/pos/inventory/csv',
        params: {
          status: this.inventory.statusId,
          search: this.inventory.search,
          ...payload
        },
        method: 'get',
        responseType: 'arraybuffer'
      })
      anchorDownload(response, name || 'inventory-csv', fileFormat(payload.extension))
    },
    async $getFilterLocation(payload) {
      if (
        this.filter.location.tempParam === JSON.stringify(payload)
      ) return

      const response = await request({
        url: '/v1/pos/settings/store-location/filter',
        params: payload,
        signal: this.filter?.location?.controller?.signal
      })

      this.filter.location.tempParam = JSON.stringify({
        search: payload.search,
        page: payload.page
      })

      this.filter.location.result = response
    },
    async $getFilterManufacturer(payload, modelOnly = false) {
      if (
        this.filter.manufacturer.tempParam === JSON.stringify(payload)
      ) return

      const response = await request({
        url: '/v1/pos/inventory/filter?manufacturer',
        params: payload,
        signal: modelOnly ? this.filter?.model?.controller?.signal : this.filter?.manufacturer?.controller?.signal
      })

      this.filter.manufacturer.tempParam = JSON.stringify(payload)

      if (modelOnly) {
        response.model = this.getPaginateArray(response.model, 10)
        this.filter.model.result = response.model
      } else {
        const keys = Object.keys(response)
        keys.forEach(key => {
          if (!!response[key] && key !== 'location') {
            response[key] = this.getPaginateArray(response[key], 10)
            this.filter[key].result = response[key]
          }
        })
      }
    },
    async $updateSoldStatus(payload) {
      return await request({
        url: `/v1/pos/inventory/sold-price/${payload.id}`,
        method: 'post',
        data: {
          ...payload.data,
          _method: 'PUT'
        }
      })
    },
    async $updateChangeStatus(payload) {
      return await request({
        url: `/v1/pos/inventory/status/${payload.id}`,
        method: 'post',
        data: {
          ...payload.data,
          _method: 'PUT'
        }
      })
    },
    async $getInventoryDeepLink(payload) {
      const response = await request({
        url: '/v1/pos/inventory/deeplink',
        method: 'get',
        params: payload
      })

      return response
    }
  }
})

export const useInventoryCrud = defineStore('inventoryCrud', {
  state: () => ({
    delete: {
      isLoading: false
    },
    reset: {
      isLoading: false
    },
    restore: {
      isLoading: false
    }
  }),
  actions: {
    async $updateNotes(payload) {
      return await request({
        url: `/v1/pos/inventory/notes/${payload.id}`,
        method: 'post',
        data: {
          ...payload,
          _method: 'PUT'
        }
      })
    },
    async $onDelete(payload) {
      return await request({
        url: `/v1/pos/inventory/${payload}`,
        method: 'post',
        data: {
          _method: 'DELETE'
        }
      })
    },
    async $onReset(payload) {
      return await request({
        url: `/v1/pos/inventory/reset/${payload}`,
        method: 'post',
        data: {
          _method: 'PUT'
        }
      })
    },
    async $onRestore(payload) {
      return await request({
        url: `/v1/pos/inventory/restore/${payload}`,
        method: 'post',
      })
    }
  }
})

export const useInventoryReconcile = defineStore('inventoryReconcile', {
  state: () => ({
    reconcile: {
      isLoading: false,
      result: null
    }
  }),
  actions: {
    async $onReconcile(payload) {
      const response = await request({
        url: '/v1/pos/inventory/data-match',
        method: 'post',
        data: payload
      })

      this.reconcile.result = response
    }
  }
})

export const useInventoryOthers = defineStore('inventoryOthers', {
  state: () => ({
    move: {
      locations: {
        isLoading: false,
        result: null
      },
      isloading: false,
      result: null
    },
    instantly: {
      isloading: false,
      result: null
    },
    phoneCheck: {
      isloading: false,
      result: null
    },
    phoneCheckPrint: {
      isloading: false,
      result: null
    }
  }),
  getters: {
    locations(state) {
      return state.move.locations?.result ?? []
    }
  },
  actions: {
    async $getMoveLocations() {
      const response = await request({
        url: `/v1/pos/inventory/move-location`,
        method: 'get',
      })
      this.move.locations.result = response
    },
    async $setMoveInventory(payload) {
      const path = payload?.id ?? 'bulk'
      await request({
        url: `/v1/pos/inventory/move-location/${path}`,
        method: 'post',
        data: payload.data
      })
    },
    async $getPhoneCheck(payload) {
      const storeImei = useImei()

      await storeImei.$getImeiCheck(payload)
      this.phoneCheck.result = storeImei.imeiCheck.result
    },
    async $getPhoneCheckPrint(payload, type) {
      const storeImei = useImei()

      await storeImei.$getImeiCheckPrint(payload, type)
      this.phoneCheckPrint.result = storeImei.imeiCheckPrint.result
    }
  }
})
