<script setup>
import { computed, defineProps, reactive, ref } from 'vue'
import { useInventoryOthers } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'
import { anchorPrint } from 'utils/element'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import PhonecheckInfo from 'components/Phonecheck/Info.vue'

const store = useInventoryOthers()
const props = defineProps({
  data: {
    type: [Object, Array],
    default: undefined
  }
})
const modal = ref(false)
const phoneCheckData = reactive({
  infoData: undefined,
  device: undefined,
  error: false
})

const loading = computed(() => {
  if (!Array.isArray(props.data)) return store?.phoneCheck?.isLoading && props?.data?.loading

  return store?.phoneCheck?.isLoading
})

const onSingleCheck = async () => {
  await tryCatchRequest(
    store.phoneCheck,
    () => store.$getPhoneCheck({
      imei: [{ id: props.data.id, imei_sn: props.data.imeisn }],
      origin: 'inventory'
    })
  )

  if (
    store?.phoneCheck?.result?.data?.length == 0 &&
    store?.phoneCheck?.result?.is_phonecheck_error !== 1
  ) {
    return ElMessage({
      message: 'IMEI/SN not found',
      type: 'warning'
    })
  } else {
    phoneCheckData.infoData = JSON.stringify(store?.phoneCheck?.result?.data[0]?.info)
    phoneCheckData.device = props.data
    if (store?.phoneCheck?.result?.data?.length && !store?.phoneCheck?.result?.is_phonecheck_error) {
      phoneCheckData.error = false
    } else {
      phoneCheckData.error = store?.phoneCheck?.result?.is_phonecheck_error
        ? store?.phoneCheck?.result?.is_phonecheck_error == 1
        : true
    }

    modal.value = true
  }
}

const onBulkCheck = async () => {
  await tryCatchRequest(
    store.phoneCheck,
    () => store.$getPhoneCheck({
      imei: props.data.map(item => ({
        id: item.id,
        imei_sn: item.imeisn
      })),
      origin: 'inventory'
    })
  )

  return store?.phoneCheck?.result?.data?.length
}

const onPrint = async () => {
  await tryCatchRequest(
    store.phoneCheckPrint,
    () => store.$getPhoneCheckPrint({
      id: phoneCheckData.device.id,
      imei: phoneCheckData.device.imeisn,
      origin: 'inventory'
    }).then(() => {
      anchorPrint(store.phoneCheckPrint.result)
      onClose()
    })
  )
}

const onCheck = async () => {
  if (Array.isArray(props.data)) {
    return await onBulkCheck()
  }
  
  return  await onSingleCheck()
}

const onClose = () => {
  modal.value = false
  phoneCheckData.error = false
}

</script>

<template>
  <div>
    <div v-loading="loading">
      <slot v-bind="{ onCheck }"></slot>
    </div>
    <Teleport to="body">
      <Modal
        v-model="modal"
        hide-footer
        @close="onClose">
        <template #header>
          <div class="w-full flex items-center">
            <h5 class="modal__content-header-title font-medium">
              {{ phoneCheckData?.device?.name ?? phoneCheckData.device?.item_name }}
            </h5>
            <Button
              view="smooth"
              color="teal"
              dense
              @click="onPrint(phoneCheckData)">
              <i class="material-icons text-lg">print</i>
            </Button>
          </div>
        </template>
        <template #default>
          <PhonecheckInfo
            v-model:data="phoneCheckData.infoData"
            :is-error="phoneCheckData.error"
          />
        </template>
      </Modal>
    </Teleport>
  </div>
</template>