<script setup>
import { onMounted, onUnmounted, reactive, ref } from 'vue'
import { useRouter, useRoute } from 'vue-router'
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'
import { format as formatDate, formatDatabase } from 'utils/date';

import Card from 'bbUi/components/card/card.vue'
import Tab from 'bbUi/components/tab/tab.vue'

import InventoryHeader from 'components/Features/Inventory/InventoryHeader.vue'
import InventoryAlerts from 'components/Features/Inventory/InventoryAlerts.vue'
import InventoryFilters from 'components/Features/Inventory/InventoryFilters.vue'
import InventoryTable from 'components/Features/Inventory/InventoryTable.vue'
import InventoryFiltersTags from 'components/Features/Inventory/InventoryFilters/InventoryFiltersTags.vue'
import CustomTableManager from 'components/CustomTable/CustomTableManager.vue'
import CustomTableExport from 'components/CustomTable/CustomTableExport.vue'

const store = useInventory()
const router = useRouter()
const route = useRoute()
const params = reactive({
  page: 1,
  limit: 10,
  total_page: 1
})
const exportData = reactive({
  extension: 'csv',
  name: '',
  startDate: null,
  endDate: null
})
const statusIdTab = ref(0)
const inventoryFilters = ref()

const onChangeExportName = () => {
  exportData.name = `Inventory ${store.tabs.find(tab => tab.id == store.inventory.statusId)?.name ?? 'Unknown'}`
}

const onInventory = async () => {
  await store.$getInventory({
    page: params.page,
    limit: params.limit
  })
}

const onChangeTab = async (statusId) => {
  if (store.inventory.statusId === statusId) return

  store.getStatusId(statusId)
  store.inventory.page = 1
  router.replace({
    params: {
      statusId: store.inventory.statusId
    }
  })
  onChangeExportName()
  await tryCatchRequest(
    store.inventory,
    onInventory
  )
}

const onExport = async () => {
  const payload = {
    extension: exportData.extension,
    ...store.inventoryExport.params
  }

  await tryCatchRequest(
    store.inventoryExport,
    () => store.$getExport(
      payload,
      `${exportData.name}-${
        exportData.startDate
          ? formatDatabase(exportData.startDate)
          : formatDatabase(new Date(Date.now()))
      }`
    )
  )
}

const onUpdateTable = async () => {
  await tryCatchRequest(
    store.inventory,
    onInventory
  )
}

const onClearTags = async (e) => {
  inventoryFilters.value.inventoryFiltersColumn.onResetFilter(e)
}

store.getStatusId(route.params?.statusId)
onChangeExportName()

onMounted(async () => {
  statusIdTab.value = store.inventory.statusId
  await tryCatchRequest(
    store.inventory,
    onInventory
  )
  await store.$getDataUser()
})

onUnmounted(() => {
  store.inventory.search = null
})

</script>

<template>
  <div class="space-y-8">
    <InventoryHeader />
    <Card class="space-y-5">
      <div class="space-y-5">
        <Tab
          v-model="statusIdTab"
          :items="store.tabs"
          by="id"
          view="smooth"
          component
          @update:modelValue="onChangeTab"
        />
        <InventoryAlerts alert-type="cost" />
        <InventoryAlerts alert-type="pending" />
        <div class="flex md:flex-row flex-col gap-4">
          <div class="flex-1">
            <InventoryFilters ref="inventoryFilters" />
          </div>
          <div class="flex-1">
            <div class="flex md:justify-end gap-x-2">
              <CustomTableManager
                :status="store.inventory.statusId"
                type="inventory"
                @update-table="onUpdateTable"
              />
              <CustomTableExport
                v-model="exportData.extension"
                :export-filename="exportData.name"
                :loading="store.inventoryExport.isLoading"
                @export="onExport"
              />
            </div>
          </div>
        </div>
        <InventoryFiltersTags @clear="onClearTags" />
        <!-- <div class="flex items-center flex-row my-4">
          <div class="inline-block mx-2" v-if="isEnablePhoneCheck && selectedInventories.length > 0">
            <Button color="teal" @click="importPhoneCheck()" dense>Import from Phonecheck</Button>
          </div>
          <div class="inline-block mx-2" v-if="selectedInventories.length > 0">
            <Button color="teal" @click="handleModalMoveInventory()" dense>Move Inventory</Button>
          </div>
          <div class="inline-block mx-2" v-if="selectedInventories.length > 0 && bulkSellInstantlyAvailable">
            <Tooltip placement="right" content="There are some inventory items that are not able to be sold instantly" :disabled="!isDisabledBulkSellInstantly">
              <div>
                <Button :disabled="isDisabledBulkSellInstantly" view="smooth" color="orange" @click="handleBulkSellInstantly" dense>Bulk Sell Instantly</Button>
              </div>
            </Tooltip>
          </div>
        </div> -->
        <InventoryTable />
      </div>
    </Card>
  </div>
</template>
