<template>
  <div>
    <Modal
      :modelValue="open"
      @update:modelValue="handleUpdateModal"
      @close="handleCloseModal"
    >
      <template #header>
        <div v-if="!stateShow.is_fetched" v-loading="loading.get">
          <h1 class="text-xl">
            Sell Instantly to
            <span class="text-teal-04">{{ state.wholeseller_name }}</span>
          </h1>
        </div>
        <div v-else v-loading="loading.show">
          <h1 class="text-xl">Offer Confirmation</h1>
        </div>
      </template>
      <template #headerRight>
        <Button
          v-if="stateShow.is_fetched"
          @click="handleDownloadPdf"
          color="teal"
          dense
          ><i class="material-icons">file_download</i> PDF</Button
        >
      </template>
      <template #default>
        <div v-if="!stateShow.is_fetched" v-loading="loading.get">
          <p class="mb-6">
            There are
            <span class="font-semibold">({{ data.order_detail.length }})</span>
            qty you selected for total
            {{ state.wholeseller_currency_symbol }}{{ totalInventoryPrice }}
            <a
              class="mx-2"
              href=""
              @click.prevent="isShowDetails = !isShowDetails"
              >{{ isShowDetails ? 'Hide' : 'Show' }} Details</a
            >
          </p>
          <transition name="fade">
            <Table v-show="isShowDetails" :data="tableData">
              <TableColumn label="Item #" width="200">
                <template #default="{ row }">
                  <p>{{ row.name }}</p>
                  <div class="my-2">
                    <p class="text-sm text-grey-03">
                      <span class="font-semibold"> Condition:</span>
                      {{ row.condition }}
                    </p>
                    <p class="text-sm text-grey-03">
                      <span class="font-semibold">IMEI/SN:</span>
                      {{ row.imei_sn }}
                    </p>
                    <p class="text-sm text-grey-03">
                      {{ row.description }}
                    </p>
                  </div>
                </template>
              </TableColumn>
              <TableColumn label="Qty">
                <template #default>1</template>
              </TableColumn>
              <TableColumn label="Cost">
                <template #default="{ row }">
                  <Currency :value="row.cost" />
                </template>
              </TableColumn>
              <TableColumn label="Price">
                <template #default="{ row }">
                  {{ state.wholeseller_currency_symbol
                  }}{{ row.price_who_seller }}
                </template>
              </TableColumn>
              <TableColumn>
                <template #default="{ row }">
                  <Button
                    v-if="isShowRemoveInventoryItem"
                    @click="handleRemoveInventoryItem(row)"
                    color="red"
                    dense
                    ><i class="material-icons">delete</i></Button
                  >
                </template>
              </TableColumn>
            </Table>
          </transition>
          <Fieldset class="my-6">
            <template #label>
              <span>Contact Information</span>
            </template>
            <template #default>
              <Input
                v-model="data.customer_email"
                :view="
                  (validation.customer_email.$invalid && 'danger') || 'default'
                "
                label="Email Address"
                block
              />
              <small
                class="text-red-base"
                v-for="(error, errorIndex) of validation.customer_email.$errors"
                :key="errorIndex"
                >{{ error.$message }}.
              </small>
            </template>
          </Fieldset>
          <Fieldset class="my-6">
            <template #label>
              <span>Payout Information</span>
            </template>
            <template #default>
              <PaymentMethod
                v-model:data="formDataPaymentMethod"
                v-if="!loading.get"
                v-loading="loading.autofill"
                ref="refPaymentMethod"
                :items="state.payment_method"
              />
            </template>
          </Fieldset>
          <Fieldset class="my-6">
            <template #label>
              <span>Free Shipping Label</span>
            </template>
            <template #default>
              <div class="grid grid-cols-2 gap-4">
                <div class="col-span-2">
                  <Checkbox
                    v-model="isUseCompanyAddress"
                    class="font-medium"
                    type="toggle"
                    >Auto fill from company location profile</Checkbox
                  >
                </div>
                <div>
                  <Input
                    v-model="data.shipping_firstname"
                    :view="
                      (validation.shipping_firstname.$invalid && 'danger') ||
                      'default'
                    "
                    block
                    label="First Name"
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_firstname
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div>
                  <Input
                    v-model="data.shipping_lastname"
                    :view="
                      (validation.shipping_lastname.$invalid && 'danger') ||
                      'default'
                    "
                    block
                    label="Last Name"
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_lastname
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div class="col-span-2">
                  <Input
                    :modelValue="data.shipping_address1"
                    @update:modelValue="
                      (value) => {
                        data.shipping_address1 = value
                        handleUpdateAddress()
                      }
                    "
                    v-gmap="address"
                    :view="
                      (validation.shipping_address1.$invalid && 'danger') ||
                      'default'
                    "
                    block
                    label="Street Address"
                  />
                  <small>Street address, P.O box, company name, c/o.</small>
                  <small
                    class="text-red-base block"
                    v-for="(error, errorIndex) of validation.shipping_address1
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div class="col-span-2">
                  <Input
                    :modelValue="data.shipping_suite"
                    @update:modelValue="
                      (value) => {
                        data.shipping_suite = value
                        handleUpdateAddress()
                      }
                    "
                    block
                    label=""
                  />
                  <small>Apartment, suite, unit, building floor, etc.</small>
                </div>
                <div>
                  <Input
                    :modelValue="data.shipping_city"
                    @update:modelValue="
                      (value) => {
                        data.shipping_city = value
                        handleUpdateAddress()
                      }
                    "
                    block
                    label="City"
                    :view="
                      (validation.shipping_city.$invalid && 'danger') ||
                      'default'
                    "
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_city
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div v-if="!['DK', 'NL'].includes(data.shipping_country)">
                  <Input
                    :modelValue="data.shipping_state"
                    @update:modelValue="
                      (value) => {
                        data.shipping_state = value
                        handleUpdateAddress()
                      }
                    "
                    block
                    label="State"
                    :view="
                      (validation.shipping_state.$invalid && 'danger') ||
                      'default'
                    "
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_state
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div>
                  <Input
                    :modelValue="data.shipping_zip"
                    @update:modelValue="
                      (value) => {
                        data.shipping_zip = value
                        handleUpdateAddress()
                      }
                    "
                    :view="
                      (validation.shipping_zip.$invalid && 'danger') ||
                      'default'
                    "
                    block
                    label="Postal Code"
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_zip
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div>
                  <Input
                    :modelValue="data.shipping_country"
                    @update:modelValue="
                      (value) => {
                        data.shipping_country = value
                        handleUpdateAddress()
                      }
                    "
                    :view="
                      (validation.shipping_country.$invalid && 'danger') ||
                      'default'
                    "
                    block
                    label="Country"
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_country
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
                <div>
                  <InputPhone
                    :modelValue="data.shipping_phone"
                    @update:modelValue="
                      (value) => {
                        data.shipping_phone = value
                        handleUpdateAddress()
                      }
                    "
                    @validation="handleIsValidPhone"
                    block
                    label="Phone"
                    country-autofill-setting
                  />
                  <small
                    class="text-red-base"
                    v-for="(error, errorIndex) of validation.shipping_phone
                      .$errors"
                    :key="errorIndex"
                    >{{ error.$message }}.
                  </small>
                </div>
              </div>
            </template>
          </Fieldset>
        </div>
        <div class="grid grid-cols-6 gap-3" v-else>
          <div class="md:col-span-3 col-span-6">
            <h2 class="text-lg">Your offer is confirmed</h2>
            <p>A record of your offer information appears below.</p>
          </div>
          <div
            class="flex items-center md:justify-end md:col-span-2 md:col-start-5 col-span-6 justify-start md:my-0 my-4"
          >
            <Tooltip
              :disabled="showOrderDetail.shipping_label_is_generate == 1"
            >
              <template #content>
                <p>
                  Unable to generate a shipping label. <br />
                  Please contact support for more detail
                </p>
              </template>
              <div>
                <Button
                  :disabled="showOrderDetail.shipping_label_is_generate == 0"
                  color="teal"
                  @click="handlePrintShippingLabel"
                  dense
                >
                  <i class="material-icons">print</i> Print Shipping Label
                </Button>
              </div>
            </Tooltip>
          </div>
          <div class="md:col-span-3 col-span-6">
            <p class="font-normal text-grey-04 my-3">
              Offer Number: <br />
              <span class="text-black">{{ showOrderDetail.box_id }}</span>
            </p>
            <p class="font-normal text-grey-04 my-3">
              Date Created: <br />
              <span class="text-black">{{ showOrderDetail.created_at }}</span>
            </p>
            <p class="font-normal text-grey-04 my-3">
              Payment Method: <br />
              <span class="text-black">{{
                showOrderDetail.payment_method
              }}</span>
            </p>
            <p class="font-normal text-grey-04 my-3">
              Contact Info: <br />
              <span class="text-black">{{
                showOrderDetail.customer_email
              }}</span>
            </p>
          </div>
          <div class="md:col-span-3 col-span-6">
            <p class="font-normal text-grey-04">Your offer to:</p>
            <div class="flex items-center">
              <i class="material-icons mr-4 text-grey-04">store</i>
              <p>{{ showOrderDetail.company_name }}</p>
            </div>
            <div class="flex items-center">
              <i class="material-icons mr-4 text-grey-04">location_on</i>
              <p>{{ showOrderDetail.company_address }}</p>
            </div>
            <div class="flex items-center">
              <i class="material-icons mr-4 text-grey-04">phone</i>
              <a :href="`tel:${showOrderDetail.company_phone}`">{{
                showOrderDetail.company_phone
              }}</a>
            </div>
            <div class="flex items-center">
              <i class="material-icons mr-4 text-grey-04">email</i>
              <p>{{ showOrderDetail.company_email }}</p>
            </div>
          </div>
          <div class="col-span-6">
            <Table :data="showOrderDetail.order_detail">
              <TableColumn label="Selling Details">
                <template #default="{ row }">
                  <p class="font-semibold">{{ row.product_name }}</p>
                  <div class="my-2">
                    <p class="font-normal text-sm text-grey-03">
                      <span class="font-semibold">IMEI/SN:</span>
                      {{ row.imei_sn }}
                    </p>
                    <p class="font-normal text-sm text-grey-03">
                      {{ row.description }}
                    </p>
                  </div>
                </template>
              </TableColumn>
              <TableColumn label="Qty" align="right">
                <template #default>1</template>
              </TableColumn>
              <TableColumn label="Price" align="right">
                <template #default="{ row }">
                  {{ state.wholeseller_currency_symbol }}{{ row.price }}
                </template>
              </TableColumn>
            </Table>
            <div
              class="flex flex-col items-end justify-end pr-2 font-semibold mt-4"
            >
              <div class="flex items-center my-1">
                <p class="mr-12">Sub Total</p>
                {{ state.wholeseller_currency_symbol
                }}{{ showOrderDetail.sub_total }}
              </div>
              <div
                v-if="
                  showOrderDetail.payment_extra_amount !== undefined &&
                  showOrderDetail.payment_extra_amount !== '0.00' &&
                  showOrderDetail.payment_extra_amount !== 0
                "
                class="flex items-center my-1"
              >
                <p class="mr-12">Payout Bonus</p>
                {{ state.wholeseller_currency_symbol
                }}{{ showOrderDetail.payment_extra_amount }}
              </div>
              <div class="flex items-center my-1">
                <p class="mr-12">Total</p>
                {{ state.wholeseller_currency_symbol
                }}{{ showOrderDetail.total }}
              </div>
            </div>
          </div>
        </div>
      </template>
      <template #footer>
        <div class="flex justify-between items-center">
          <a
            v-if="!stateShow.is_fetched"
            class="block py-4 pr-6 select-none text-red-04 hover:text-red-03"
            color="red"
            @click.prevent="handleCloseModal"
            >Cancel</a
          >
          <div v-else></div>
          <Button
            v-if="!stateShow.is_fetched"
            color="teal"
            :loading="loading.create"
            @click="handleBulkSellInstantly"
            >Confirm</Button
          >
          <Button
            v-else
            color="teal"
            :loading="loading.create"
            @click="handleRedirectToInvoice"
            >Go to Invoice</Button
          >
        </div>
      </template>
    </Modal>
  </div>
</template>

<script>
import { computed, defineComponent, reactive, ref, watch } from 'vue'
import { useRouter } from 'vue-router'
import { useBulkSellInstantly } from 'composable/inventory'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

import PaymentMethod from 'components/Inventory/PaymentMethod.vue'
import InputPhone from 'components/InputPhone.vue'
import Currency from 'components/Currency.vue'

import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'
import { helpers, email } from '@vuelidate/validators'
import { ElMessage } from 'element-plus'
import { money } from 'utils/number'

export default defineComponent({
  components: {
    Fieldset,
    Button,
    Input,
    Modal,
    Checkbox,
    PaymentMethod,
    InputPhone,
    Currency
  },
  props: {
    open: {
      type: Boolean,
      default: false
    },
    inventories: {
      type: Array,
      default: () => []
    }
  },
  emits: ['update:open', 'complete', 'close'],
  setup(props, { emit }) {
    const {
      loading,
      modal,
      state,
      stateShow,
      formData: data,
      formDataPaymentMethod,
      isShowDetails,
      actionGet,
      actionShow,
      actionCreate,
      resetData
    } = useBulkSellInstantly()
    const router = useRouter()
    const refPaymentMethod = ref(null)
    const isValidPhone = ref(false)
    const isUseCompanyAddress = ref(true)
    // const savedTemporaryAddress = reactive({
    //   shipping_address1: null,
    //   shipping_city: null,
    //   shipping_state: null,
    //   shipping_zip: null,
    //   shipping_phone: null,
    //   shipping_country: null,
    //   shipping_suite: null
    // })

    const rules = {
      customer_email: {
        required,
        email: helpers.withMessage(
          'Customer email is not a valid email address',
          email
        )
      },
      shipping_firstname: {
        required: helpers.withMessage('First name is required', required)
      },
      shipping_lastname: {
        required: helpers.withMessage('Last name is required', required)
      },
      shipping_address1: {
        required: helpers.withMessage('Street address is required', required)
      },
      shipping_city: {
        required: helpers.withMessage('City is required', required)
      },
      shipping_state: {
        required: helpers.withMessage('State is required', required)
      },
      shipping_zip: {
        required: helpers.withMessage('Postal code is required', required)
      },
      shipping_phone: {
        required: helpers.withMessage('Phone number is required', required),
        invalid: helpers.withMessage(
          'Invalid phone number',
          () => isValidPhone.value
        )
      },
      shipping_country: {
        required: helpers.withMessage('Country is required', required)
      }
    }

    const payloadData = computed(() => {
      return {
        ...data,
        ...formDataPaymentMethod.value
      }
    })
    const totalInventoryPrice = computed(() => {
     const totalPrice = data.order_detail.reduce((curr, next) => {
        const cleanedPrice = parseFloat(next.price_who_seller.replace(/,/g, ''))
        curr += cleanedPrice
        return curr
      }, 0)
      return money(totalPrice || 0)
    })
    const isShowRemoveInventoryItem = computed(() =>
      Array.isArray(data.order_detail) ? data.order_detail.length > 1 : false
    )
    const tableData = computed(() =>
      data.order_detail.map((order) => ({
        ...order,
        price_who_seller: money(order.price_who_seller || 0)
      }))
    )
    const showOrderDetail = computed(() => ({
      ...stateShow,
      sub_total: money(stateShow.sub_total || 0),
      payment_extra_amount: money(stateShow.payment_extra_amount || 0),
      total: money(stateShow.total || 0),
      order_detail: stateShow.order_detail.map((order) => ({
        ...order,
        price: money(order.price || 0)
      }))
    }))

    let validation = useVuelidate(rules, payloadData, {
      $lazy: true
    })
    const validate = () => {
      const isValidPaymentMethod = refPaymentMethod.value?.validate()
      validation.value.$validate()
      return !validation.value.$invalid && isValidPaymentMethod
    }
    const resetValidation = () => {
      validation.value.$reset()
    }

    const actionGetBulkInstantly = async () => {
      await actionGet(
        {
          ids: props.inventories.map((inventory) => inventory.id).join(','),
          sources: props.inventories
            .filter((inventory) => !!inventory.price_sharing)
            .map((inventory) => inventory.price_sharing)
            .join(',')
        },
        (err) => {
          if (err) handleCloseModal()
        }
      )
    }

    const handleCloseModal = () => {
      isUseCompanyAddress.value = !isUseCompanyAddress.value
        ? true
        : isUseCompanyAddress.value
      resetData()
      resetValidation()
      emit('update:open', false)
      emit('close', true)
    }
    const handleBulkSellInstantly = async () => {
      if (['DK'].includes(data.shipping_country)) {
        delete rules.shipping_state
        validation = useVuelidate(rules, payloadData, {
          $lazy: true
        })
      }
      const isValid = validate()
      if (isValid)
        await actionCreate(async (err, data) => {
          if (err) return handleCloseModal()
          await actionShow(data, (err) => {
            if (err) handleCloseModal()
          })
        })
    }
    const handleUpdateModal = (e) => {
      emit('update:open', e)
    }
    const handleIsValidPhone = (isValid) => (isValidPhone.value = isValid)
    const handlePrintShippingLabel = () =>
      window.open(stateShow.shipping_label || '#', '_blank')
    const handleDownloadPdf = () => window.open(stateShow.pdf || '#', '_blank')
    const handleRedirectToInvoice = () => {
      if (!stateShow.invoice_id)
        return ElMessage({
          type: 'error',
          message: 'Invoice ID not found'
        })
      router.push({
        name: 'invoiceDetail',
        params: {
          buybackId: stateShow.invoice_id
        }
      })
    }
    const handleRemoveInventoryItem = (row) => {
      const { inventory_id: inventoryId } = row
      if (!inventoryId) return null
      data.order_detail = data.order_detail.filter(
        (inventoryItem) => inventoryItem.inventory_id !== inventoryId
      )
    }
    const handleUpdateAddress = () => {
      const isAddressExacylyMatchWithCompanyData =
        data.shipping_address1 === state.company.address &&
        data.shipping_city === state.company.city &&
        data.shipping_state === state.company.state &&
        data.shipping_zip === state.company.zip &&
        data.shipping_phone === state.company.phone &&
        data.shipping_country === state.company.country &&
        data.shipping_suite === state.company.suite

      if (isAddressExacylyMatchWithCompanyData) {
        isUseCompanyAddress.value = true
        return
      }

      if (isUseCompanyAddress.value) isUseCompanyAddress.value = false
    }

    watch(
      () => isUseCompanyAddress.value,
      (isUseCompanyAddress) => {
        if (isUseCompanyAddress) {
          // Object.assign(savedTemporaryAddress, {
          //   shipping_address1: data.shipping_address1,
          //   shipping_city: data.shipping_city,
          //   shipping_state: data.shipping_state,
          //   shipping_zip: data.shipping_zip,
          //   shipping_phone: data.shipping_phone,
          //   shipping_country: data.shipping_country,
          //   shipping_suite: data.shipping_suite
          // })
          Object.assign(data, {
            shipping_address1: state.company.address,
            shipping_city: state.company.city,
            shipping_state: state.company.state,
            shipping_zip: state.company.zip,
            shipping_phone: state.company.phone,
            shipping_country: state.company.country,
            shipping_suite: state.company.suite
          })
        }
        // else {
        //   Object.assign(data, {
        //     ...savedTemporaryAddress
        //   })
        // }
      }
    )
    watch(
      () => props.open,
      async (isOpen) => {
        if (isOpen) await actionGetBulkInstantly()
      }
    )
    watch(
      () => stateShow.is_fetched,
      (isComplete) => {
        if (isComplete) emit('complete', true)
      }
    )

    return {
      refPaymentMethod,
      loading,
      modal,
      state,
      stateShow,
      data,
      formDataPaymentMethod,
      isShowDetails,
      isUseCompanyAddress,
      isShowRemoveInventoryItem,
      actionGet,
      payloadData,
      totalInventoryPrice,
      tableData,
      showOrderDetail,
      address: {
        route: 'shipping_address1',
        locality: 'shipping_city',
        administrative_area_level_1: 'shipping_state',
        country: 'shipping_country',
        postal_code: 'shipping_zip',
        noComponentRestriction: true
      },
      validation,
      validate,
      handleBulkSellInstantly,
      handleUpdateModal,
      handleCloseModal,
      handleIsValidPhone,
      handlePrintShippingLabel,
      handleDownloadPdf,
      handleRedirectToInvoice,
      handleRemoveInventoryItem,
      handleUpdateAddress
    }
  }
})
</script>
