<script setup>
import { onMounted, ref, watch, defineEmits } from 'vue'
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'
import { format as formatDate } from 'utils/date'

const store = useInventory()
const tags = ref([])

const  emits = defineEmits([
  'clear'
])

const isDateKey = (key) => ['sold', 'added', 'date'].includes(key)

const formatTagValue = (key, value) => {
  if (Array.isArray(value)) {
    if (isDateKey(key)) {
      const formatted = value.filter(Boolean).map(formatDate)
      return {
        raw: formatted,
        display: formatted.join(' - ')
      }
    }
    return {
      raw: value,
      display: value.join(', ')
    }
  }

  if (typeof value === 'object' && value?.name) {
    return {
      raw: value,
      display: value.name
    }
  }

  return {
    raw: value,
    display: value
  }
}

const setTags = () => {
  const keys = Object.keys(store.filterTags)
  const result = keys.map(key => {
    const rawValue = store.filterTags[key]
    const { raw, display } = formatTagValue(key, rawValue)

    return {
      name:
        key === 'manufacturer' ? 'brand' :
        key === 'sold' ? 'date by sold' :
        key === 'added' ? 'date by added' :
        key, // fallback
      key,
      value: raw,
      displayValue: display
    }
  })

  tags.value = result.filter(tag => {
    const val = tag.value
    if (val == null) return false
    if (typeof val === "string") return val.trim() !== ""
    if (Array.isArray(val)) return val.length > 0
    if (typeof val === "object") return Object.keys(val).length > 0 && !!val.id
    return true
  })
}

const onRemove = (key = undefined) => {
  if (!key) {
    emits('clear')
    return
  }

  setTags()
  emits('clear', key)
}

const onUpdateTags = async () => {
  if (!store.inventory.isLoading) {
    if (store.inventory.page != 1) this.store.inventory.page = 1

    await tryCatchRequest(
      store.inventory,
      () => store.$getInventory()
    )
  }
}

watch(() => store.filterTags, (newVal, oldVal) => {
  if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
    setTags()
    onUpdateTags()
  }
})

</script>

<template>
  <div class="flex gap-2">
    <div v-for="tag in tags" :key="tag.name">
      <div class="btn btn-smooth btn-grey btn-dense">
        <div class="flex items-center gap-x-2">
          <p class="font-medium capitalize">
            {{ tag.name }}:
          </p>
          {{ tag.displayValue }}
          <i class="material-icons text-sm cursor-pointer" @click="onRemove(tag.key)">close</i>
        </div>
      </div>
    </div>

    <div v-if="tags?.length">
      <div
        class="btn btn-smooth btn-grey btn-dense"
        @click="onRemove(undefined)">
        <div class="flex items-center gap-x-2">
          <i class="material-icons text-sm cursor-pointer">close</i>
          <p class="font-medium">Clear All</p>
        </div>
      </div>
    </div>
  </div>
</template>
