<script setup>
import { ref, computed } from 'vue'
import { useInventoryReconcile, useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'

import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import { ElMessage } from 'element-plus'

const storeInventory = useInventory()
const store = Â useInventoryReconcile()
const modal = ref(false)
const imei = ref()
const currentStep = ref(0)
const affected = ref({})
const confirm = ref(0)

const isDisabled = computed(() => {
  return store.reconcile?.result?.imei?.length ? false : true
})

const onShowForm = () => {
  modal.value = true
  if (store.reconcile.result) {
    currentStep.value = 1
    confirm.value = 1
    affected.value = store.reconcile.result
  }
}

const onCancel = () => {
  modal.value = false
}

const onProcess = async () => {
  switch (currentStep.value) {
    case 0:
      await tryCatchRequest(
        store.reconcile,
        () => store.$onReconcile({
          confim: confirm.value,
          imei_sn: JSON.stringify(imei.value.split('\n'))
        }).then(() => {
          confirm.value = 1
          affected.value = store.reconcile.result
        })
      )
      currentStep.value++
      break
    case 1:
      await tryCatchRequest(
        store.reconcile,
        () => store.$onReconcile({
          confim: confirm.value,
          imei_sn: JSON.stringify(imei.value.split('\n'))
        }).then(() => {
          affected.value = {}
          confirm.value = 0
          imei.value = ''
          modal.value = false
          store.reconcile.result = null
          currentStep.value = 0
          ElMessage({
            type: 'success',
            message: 'Changes saved'
          })
        })
      )
      await tryCatchRequest(
        storeInventory.inventory,
        () => store.$getIventory()
      )
      break
  }
}

</script>

<template>
  <div>
    <div @click="onShowForm">
      <slot name="default" />
    </div>
    <Teleport to="body">
      <Modal
        v-model="modal">
        <template #header>
          <h6 class="font-medium">Reconcile Inventory</h6>
        </template>
        <div 
          v-loading="store.reconcile.isLoading">
          <TransitionGroup name="fade">
            <div v-if="currentStep === 0">
              <Input
                v-model="imei"
                type="textarea"
                block
                rows="6">
                <template #label>
                  <p class="font-medium">Scan IMEI or Serial Numbers into it's own row</p>
                </template>
              </Input>
            </div>
            <div v-else-if="currentStep === 1">
              <Alert
                class="mb-6"
                color="red"
                view="smooth">
                <span class="font-bold">
                  {{ affected.total ?? 0 }} Item(s)
                  will be deleted from your inventory
                </span>.
                You can restore these items if needed within the deleted tab.
              </Alert>
              <Table :data="affected.imei">
                <TableColumn label="" width="50">
                  <template #default="{ $index }">
                    {{ $index + 1 }}
                  </template>
                </TableColumn>
                <TableColumn prop="imei" label="IMEI/SN" />
                <TableColumn prop="name" label="Item" />
              </Table>
            </div>
            <div v-else>
              <p class="text-center text-red-04">Something went wrong</p>
            </div>
          </TransitionGroup>
        </div>
        <template #footer>
          <div class="flex flex-wrap items-center justify-between">
            <a
              class="text-red-04 py-4 px-4 hover:text-red-02"
              @click.prevent="onCancel">
              Cancel
            </a>
            <Button
              color="teal"
              :disabled="isDisabled"
              :loading="store.reconcile.isLoading"
              @click="onProcess">
              {{ currentStep === 1 ? 'Proceed' : 'Continue' }}
            </Button>
          </div>
        </template>
      </Modal>
    </Teleport>
  </div>
</template>
