<script setup>
import { computed, defineEmits, reactive, ref, watch, defineProps, onMounted } from 'vue'
import { useInventory } from 'store/pinia/inventory'
import { tryCatchRequest } from '@/utils/request'

import Input from 'bbUi/components/input/input.vue'
import Badge from "bbUi/components/badge/badge.vue"
import { ElMessage } from 'element-plus'
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import Loading from 'bbUi/components/loading/loading.vue'

const STATUS_COLOR_MAP = {
  'Available': 'teal',
  'Sold': 'grey',
  'Deleted': 'red',
  'Pending': 'orange',
  'Not Available': 'red'
}

const props = defineProps({
  item: {
    type: Object,
    default: undefined
  }
})
const emits = defineEmits([
  'change'
])
const store = useInventory()
const status = ref(props.item.status)
const color = computed(() => onGetColorStatus(status.value || 'Not Available'))
const statusList = reactive([
  {name: 'Available', id: 'available'},
  {name: 'Sold', id: 'sold'},
  {name: 'Not Available', id: 'not available'}
])
const sold = ref(0)
const modalSold = ref(false)

const onChangeStatus = async (e) => {
  if (status.value == 'Sold') {
    modalSold.value= true
    return
  }
  if (status.value === 'Not Available') {
    await tryCatchRequest(
      store.status,
      () => store.$updateChangeStatus({
        id: props.item.id,
        data: {
          status_id: 5 // for not available
        }
      }).then(() => {
        emits('change', status.value)
      })
      .catch(() => {
        status.value = props.item.status
      })
    )
  }
}
const onCloseModal = () => {
  modalSold.value = false
  status.value = props.item.status
}
const onSaveSold = async () => {
  await tryCatchRequest(
    store.status,
    () => store.$updateSoldStatus({
      id: props.item.id,
      data: {
        sold: sold.value
      }
    }).then(() => {
      ElMessage({
        message: 'Inventory status updated.',
        type: 'success'
      })
      modalSold.value = false
      emits('change', status.value)
    })
  )
}

const onGetColorStatus = (status) => STATUS_COLOR_MAP[status] || ''


status.value = props?.item?.status || undefined
</script>

<style lang="scss" scoped>
  .select-status {
    background-color: #e7f2ff;
    color: #1672ec;
    font-size: 9px;

    option {
      color: #616161
    }
  }

  .input__icon {
    border-right: 1px solid #eaeaea;
  }
</style>

<template>
  <div
    v-loading="store.status.isLoading">
    <Badge
      v-if="status != 'Available'"
      class="whitespace-nowrap"
      :color="color">
      {{ status }}
    </Badge>
    <select
      v-else
      v-model="status"
      class="py-1 px-4 rounded-lg select-status"
      @change="onChangeStatus"> 
      <option
        v-for="(item, i) in statusList"
        :key="item"
        :tabindex="`${i + 1}`"
        :value="item.name"
        class="bg-white hover:bg-white text-base focus:bg-white cursor-pointer">
        {{ item.name }}
      </option>
    </select>
  </div>
  <Transition name="fade">
    <Loading v-if="store.status.isLoading" />
  </Transition>

  <Modal
    v-model="modalSold"
    size="xs"
    @close="onCloseModal">
    <template #header>
      <div>
        <h6>Set Sold Price</h6>
      </div>
    </template>
    <template #default>
      <div>
        <Input
          v-model="sold"
          type="number"
          placeholder="00.00"
          class="w-full"
          label="Sold Price"
          icon>
          <template #icon>
            {{ store.user.currency_symbol }}
          </template>
        </Input>
      </div>
    </template>
    <template #footer>
      <div class="flex items-center justify-between gap-x-4">
        <Button
          color="red"
          view="outlined"
          @click="onCloseModal">
          Cancel
        </button>
        <Button
          class="rounded-xl"
          color="teal"
          view="outlined"
          :loading="store.status.isLoading"
          @click="onSaveSold">
          Save
        </Button>
      </div>
    </template>
  </Modal>
</template>

